@import url('https://fonts.googleapis.com/css2?family=Encode+Sans+SC:wght@400;700&display=swap');

html,
body,
#root {
    height: 100%;
}

#root {
    font-family: 'Encode Sans SC', sans-serif;
}

.container {
    min-height: 100vh;
    background: #dfe5eb;
}

.loader {
    font-size: 18px;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container>main {
    padding: 0 0 50px;
}

.movies {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 20px 0;
}

