@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

.paging{
    font-size: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.paging .page-number {
    font-family: 'Roboto', sans-serif;
    margin: 0 30px;
}
.paging button {
    cursor: pointer;
    width: 50px;
    height: 50px;
    text-align: center;
}


.padding {
    width: 50px;
}