@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Encode+Sans+SC:wght@400;700&display=swap);
.movie {
    position: relative;
    display: -webkit-flex;
    display: flex;
    width: 700px;
    background: #fafafa;
    margin: 35px 50px;
    padding: 30px 25px 0px ;
    box-sizing: border-box;
    box-shadow: 1px 1px 10px .5px rgba(100, 100, 100, 0.3);
}

.movie img {
    position: relative;
    margin-right: 35px;
    height: 300px;
    top: -50px;
    box-shadow: 1px 1px 20px 0px rgba(50,50,50,.8);
}
.movie .title {
    font-size: 25px;
    margin-bottom: 7px;
}
.movie .year {
    font-size: 15px;
    margin-bottom: 10px;
}

.movie .genres {
    font-size: 14px;
    color: #aaa;
    display: -webkit-flex;
    display: flex;
    margin-bottom: 10px;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
    .movie .genres>li+li::before {
        content: ", ";
    }

.movie .rating {
    margin-bottom: 20px;
}
.movie .rating>span {
    font-weight: bold;
    color: rgb(245, 59, 35);
}
.movie .trailer {
    position: absolute;
    bottom: 0;
    right: 0;
    display: -webkit-inline-flex;
    display: inline-flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 110px;
    height: 35px;
    background: rgb(55, 55, 55);
    color: white;
}

.movie .trailer>span {
    position: relative;
    transition: 0.3s ease-out;
}
    .movie .trailer:hover>span {
        padding-right: 8px;
    }

.movie .trailer>span::after {
    opacity: 0;
    position: absolute;
    right: 0;
    content: '\00bb';
    transition: .3s ease-out;
}
    .movie .trailer:hover>span::after {
        opacity: 1;
        right: -5px;
    }

.paging{
    font-size: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.paging .page-number {
    font-family: 'Roboto', sans-serif;
    margin: 0 30px;
}
.paging button {
    cursor: pointer;
    width: 50px;
    height: 50px;
    text-align: center;
}


.padding {
    width: 50px;
}
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
        margin: 0;
        padding: 0;
        border: 0;
        font-size: 100%;
        font: inherit;
        vertical-align: baseline;
        -webkit-user-select: none;
            -ms-user-select: none;
                user-select: none;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
        display: block;
}
body {
        line-height: 1;
}
ol, ul {
        list-style: none;
}
blockquote, q {
        quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
        content: '';
        content: none;
}
table {
        border-collapse: collapse;
        border-spacing: 0;
}

button, input[type=submit]{
        all:unset
}

input{
        outline: 0;
}
a{
        color: black;
        text-decoration: none;
}
